export default {
	data() {
		return {
			userName: '',
			passwd:'',
			
		}
	},
	onShow() {
		
	},
	onLoad() {
		
	}, 
	methods: {
		doLogin(){
			let self = this;
			uni.hideKeyboard();
			//验证手机号码
			if(!self.userName){ 
				uni.showToast({title: '请填写用户名',icon:"none"});
				return false; 
			}
			if(!self.passwd){ 
				uni.showToast({title: '请填写密码',icon:"none"});
				return false; 
			}
			uni.showLoading({
				title: '登录中...'
			})
			
			
			//模板示例比对本地储存的用户信息，实际使用中请替换为上传服务器比对。
			setTimeout(()=>{
				uni.request({
					url: 'https://wxshop.qclike.cn/api/admins/login', 
					method:'POST',
					header:{
						'content-type': 'application/x-www-form-urlencoded', 
					},
					data:{
						account:self.userName,
						password:self.passwd
					},
					success: (res) => {
						uni.hideLoading();
						if(res.data.code  == 1){
							var result = res.data.data.userinfo;
							var provider={};
							provider.nickname = result.nickname;
							provider.token = result.token;
							//存下本地的数据
							uni.setStorage({//缓存用户登陆状态
								key: 'userInfo',  
								data: provider  
							}) 
							self.$nextTick(function(){
								//登录成功跳转到首页
								uni.navigateTo({
									url: '/pages/tabBar/home'
								})
							})
						}
						if(res.data.code  == 0){
							let msg = res.data.msg || '请输入正确的用户名或密码';
							uni.showToast({title: msg,icon:"none"});
						}
					}
				});
			},1000)
		}
	}
}
